/* eslint-disable react/style-prop-object */
import React, { Component } from "react";
// import { random } from "./../util/random";
import "./Spinner.sass";

class Spinner extends Component {
  constructor(props) {
    super(props);
    this.loadHandler = this.props.loadHandler.bind(this);
    this.state = {
      tick: 24,
      sec : 1000
    };
  }
  isLoaded(){
    let loaded = this.props.loaded;
    if(loaded) this.animate();
    return (loaded===true)?"loaded":"loading";
  }
  componentDidMount(){
  }
  animate(){
    let element = document.getElementById('spinner');
    this.fade(element, 1000);
  }
  fade(element, duration) {
    console.log("페이드 애니메이션 시작");
    
    let op = 1;
    let time = this.state.tick/this.state.sec * 1000;
    let minusValue = 1 / (duration/this.state.tick);

    let anime = setInterval(function() {
      if (op <= 0.1) {
        clearInterval(anime);
        element.style.display = "none";
      }
      element.style.opacity = op;
      element.style.filter = "alpha(opacity=" + op * 100 + ")";
      op -= minusValue;
      
    }, time);
  }
  render() {
    return (
      <div id="spinner" className={"spinner "+ this.isLoaded()}>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="251px"
          height="251px"
          viewBox="0 0 100 100"
          preserveAspectRatio="xMidYMid"
        >
          <circle
            cx="50"
            cy="50"
            r="19.2892"
            fill="none"
            stroke="#e90c59"
            strokeWidth="2"
          >
            <animate
              attributeName="r"
              repeatCount="indefinite"
              dur="3.3333333333333335s"
              values="0;40"
              keyTimes="0;1"
              keySplines="0 0.2 0.8 1"
              calcMode="spline"
              begin="-1.6666666666666667s"
            ></animate>
            <animate
              attributeName="opacity"
              repeatCount="indefinite"
              dur="3.3333333333333335s"
              values="1;0"
              keyTimes="0;1"
              keySplines="0.2 0 0.8 1"
              calcMode="spline"
              begin="-1.6666666666666667s"
            ></animate>
          </circle>
          <circle
            cx="50"
            cy="50"
            r="37.0357"
            fill="none"
            stroke="#46dff0"
            strokeWidth="2"
          >
            <animate
              attributeName="r"
              repeatCount="indefinite"
              dur="3.3333333333333335s"
              values="0;40"
              keyTimes="0;1"
              keySplines="0 0.2 0.8 1"
              calcMode="spline"
            ></animate>
            <animate
              attributeName="opacity"
              repeatCount="indefinite"
              dur="3.3333333333333335s"
              values="1;0"
              keyTimes="0;1"
              keySplines="0.2 0 0.8 1"
              calcMode="spline"
            ></animate>
          </circle>
        </svg>
      </div>
    );
  }
}
export default Spinner;
